import "./styles.css";
import * as React from "react";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";
import Youtube from "react-youtube";
import { useState } from "react";

export default function LabTabs() {
  const [value, setValue] = React.useState("1");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const options = {
    playerVars: {
      showinfo: 0,
      // controls: 0,
      rel: 0
    }
  };

  const [outlined, setOutlined] = useState(false);
  const onFocus = () => {
    setOutlined(true);
  };
  const onBlur = () => {
    setOutlined(false);
  };

  return (
    <div>
      <h1>MY FAVORITE ARTIST</h1>

      <Box sx={{ width: "100%", typography: "body1" }}>
        <TabContext value={value}>
          <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
            <TabList onChange={handleChange} aria-label="lab API tabs example">
              <Tab label="LILNIINA" value="1" />
              <Tab label="BOKU NO LYRIC NO BOYOMI" value="2" />
              <Tab label="MACO MARETS" value="3" />
            </TabList>
          </Box>
          <TabPanel value="1">
            <div className="App">
              <div>
                <h1>Lonely Cat</h1>
                <Youtube
                  videoId={"cb9Jj6brmF4"}
                  opts={options} // defaults -> null
                />

                <h1>Cry</h1>
                <Youtube
                  videoId={"BfmfIfYAPK8"}
                  opts={options} // defaults -> null
                />

                <h1>akumachan</h1>
                <Youtube
                  videoId={"262RpuHNVnA"}
                  opts={options} // defaults -> null
                />

                <br></br>
                <br></br>
                <br></br>
                <br></br>

                <a
                  href="https://open.spotify.com/artist/2sQQn4m6eLqk0wt3TOkaGw?si=AaOFhUxfT-qSv68dlq7qqA"
                  onFocus={onFocus}
                  onBlur={onBlur}
                  style={{
                    backgroundColor: "#3CB371",
                    color: "white",
                    padding: "14px 25px",
                    outline: outlined
                      ? "5px auto -webkit-focus-ring-color"
                      : "none"
                  }}
                  // style={{ backgroundColor: '#f44336', color: 'white', padding: '14px 25px', outline: outlined ? 'none' : '5px auto -webkit-focus-ring-color' }}
                >
                  Spotify
                </a>
              </div>
            </div>
          </TabPanel>
          <TabPanel value="2">
            <div>
              <h1>sub/objective</h1>
              <Youtube
                videoId={"HKThn7Phxkk"}
                opts={options} // defaults -> null
              />

              <h1>Be Noble</h1>
              <Youtube
                videoId={"dlMN-_8dxos"}
                opts={options} // defaults -> null
              />

              <h1>朝焼けと熱帯魚</h1>
              <Youtube
                videoId={"RgupfGqJWbk"}
                opts={options} // defaults -> null
              />

              <br></br>
              <br></br>
              <br></br>
              <br></br>

              <a
                href="https://open.spotify.com/artist/2sQQn4m6eLqk0wt3TOkaGw?si=AaOFhUxfT-qSv68dlq7qqA"
                onFocus={onFocus}
                onBlur={onBlur}
                style={{
                  backgroundColor: "#3CB371",
                  color: "white",
                  padding: "14px 25px",
                  outline: outlined
                    ? "5px auto -webkit-focus-ring-color"
                    : "none"
                }}
                // style={{ backgroundColor: '#f44336', color: 'white', padding: '14px 25px', outline: outlined ? 'none' : '5px auto -webkit-focus-ring-color' }}
              >
                Spotify
              </a>
            </div>
          </TabPanel>
          <TabPanel value="3">
            <div>
              <h1>Moondancer</h1>
              <Youtube
                videoId={"MbvpaWiuPlw"}
                opts={options} // defaults -> null
              />

              <h1>Hum!</h1>
              <Youtube
                videoId={"K7fefF0-wmk"}
                opts={options} // defaults -> null
              />

              <h1>Sparkle</h1>
              <Youtube
                videoId={"ljDsRM7-lhY"}
                opts={options} // defaults -> null
              />

              <br></br>
              <br></br>
              <br></br>
              <br></br>

              <a
                href="https://open.spotify.com/artist/2sQQn4m6eLqk0wt3TOkaGw?si=AaOFhUxfT-qSv68dlq7qqA"
                onFocus={onFocus}
                onBlur={onBlur}
                style={{
                  backgroundColor: "#3CB371",
                  color: "white",
                  padding: "14px 25px",
                  outline: outlined
                    ? "5px auto -webkit-focus-ring-color"
                    : "none"
                }}
                // style={{ backgroundColor: '#f44336', color: 'white', padding: '14px 25px', outline: outlined ? 'none' : '5px auto -webkit-focus-ring-color' }}
              >
                Spotify
              </a>
            </div>
          </TabPanel>
        </TabContext>
      </Box>
    </div>
  );
}
